* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}
body{

    overflow-x: hidden;
}
.app {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #302E2B;
    min-width: 850px;
}

.opacity80 {
    opacity: 0.5;
}

.opacity50 {
    opacity: 0.5;
}

.board {
    width: calc(75vh);
    height: calc(75vh);
    display: flex;
    flex-wrap: wrap;
    margin-right: 32px;
}

.cell {
    width: calc(75vh / 8);
    height: calc(75vh / 8);
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.cell img {
    height: 100%;
    width: 100%;
    position: relative;
    user-select: none;
}

.black {
    background-color: #739552;
}

.white {
    background-color: #ebecd0;
}

.selected {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgb(255, 255, 51) !important;
    opacity: 0.5;
}

.available {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 33%;
    height: 33%;
    background-color: rgba(0, 0, 0, 0.14) !important;
    border-radius: 50%;
    z-index: 1;
}

.captureAvailable {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 2;
    border: 5px solid rgba(0, 0, 0, 0.14);
    border-radius: 50%;

}

.checkedKing {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 0;
    background-color: rgba(255, 100, 100, 0.5);
}

.lost {
    height: calc(50vh - 60px);
    padding: 30px;
    margin-left: 50px;
    background-color: lightgrey;
}

.sidebar{
    position: relative;
}

.timerContainer {
    font-size: 24px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    user-select: none;
    background-color: rgba(0,0,0,.14);
    height: calc(75vh);
    padding: 16px 16px 0 16px;
    width: 33vw;
}

.timer {
    border-radius: 3px;
    padding: 0 10px 0 40px;
    width: 135px;
    display: flex;
    justify-content: end;
    align-items: center;
    height: 40px;
    position: relative;
    margin-bottom: 16px;
}

.clockIcon {
    position: absolute;
    left: 10px;
}

.timerBlack {
    color: #fff;
    fill: #fff;
    background-color: #262421;
}

.timerWhite {
    color: #262421;
    fill: #262421;
    background-color: #fff;
}

.restartTimer{
    background-color: hsla(0, 0%, 100%, .08);
    fill: hsla(0, 0%, 100%, .8);
    border-radius: 5px;
    transition-duration: 0.1s;
    transition-property: box-shadow, background-color, color, fill;
    transition-timing-function: ease-out;
    border: none;
    padding: 5px 20px;
    height: 48px;
    width: 126px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: inset 0 calc(.1rem*-1) 0 0 rgba(0,0,0,.25);
    cursor: pointer;
    margin-bottom: 16px;
}
.restartTimer:hover{
    background-color: hsla(0, 0%, 100%, .16);
    fill: hsla(0, 0%, 100%, 100);
}
.restartTimer span{
    color: hsla(0, 0%, 100%, .72);
    font-family: "Roboto", sans-serif;
    font-size: 15px;
    font-weight: 600;
}
.restartTimer:hover span{
    color: rgb(255, 255, 255);
}
.moveHistoryPlaceholder{
    width: 100%;
    height: calc(75vh - 64px - 56px * 2 - 16px);
}
.moveHistory{
    position: absolute;
    overflow-y: auto;
    height: calc(75vh - 64px - 56px * 2 - 32px);
    width: calc(33vw);
    top: 72px;
    color: hsla(0,0%,100%,.5);
    font-weight: 500;
    font-family: "Roboto", sans-serif;
    border-top: 1px solid hsla(0,0%,100%,.1);
    background-color: rgba(30, 30, 30, 0.14);
    scrollbar-color: rgba(0,0,0,.45) transparent;
    scrollbar-width: thin;
}
.row{
    display: flex;
    align-items: center;
    height: 30px;
    padding: 0 15px;
    position: relative;
}
.light-row {
    background-color: hsla(0,0%,100%,.02);
}
.dark-row {
    background-color: transparent;
}
.moveNodeWhite{
    position: absolute;
    color: hsla(0,0%,100%,.72);
    left: 55px;
}
.moveNodeBlack{
    position: absolute;
    color: hsla(0,0%,100%,.72);
    left: 155px;
}